import { Component } from "react"
import styled from "styled-components"
import { ApiGeneric } from "src/services/apiServices"
import { HUtils } from "@macashipo/mlib"
import { HUI } from "src/core/helpers"
const WrapLayout = styled.div``

class MUINewProject extends Component {
  constructor(props) {
    super(props)
    this.state = {
      name: "",
      loading: false,
    }
  }
  componentDidMount() {}
  requestAddProject = () => {
    this.setState(
      {
        loading: true,
      },
      () => {
        ApiGeneric({
          request: {
            method: "POST",
            path: "ProjectSimple",
            name: "Add",
          },
          data: {
            Name: this.state.name,
          },
          successCallBack: response => {
            this.setState({ loading: false })
            HUtils.runFuntion(this.props.fnList, "reloadList")
            HUtils.runFuntion(this.props.fnList, "hideModal")
          },
          errorCallBack: err => {
            console.log(err)
            this.setState({ loading: false })
          },
        })
      }
    )
  }
  render() {
    console.warn("render", this.props)
    return (
      <WrapLayout>
        <input
          type="text"
          className="form-control"
          autoFocus
          value={this.state.name}
          onChange={ev => {
            this.setState({ name: ev.currentTarget.value })
          }}
          placeholder="Project Name"
        />
        <button
          style={{ marginTop: "10px" }}
          className="btn btn-primary"
          disabled={this.state.loading}
          onClick={() => {
            if (this.state.name && this.state.name.length > 0) {
              this.requestAddProject()
            } else {
              HUI.showToastWarning("Please enter project name!")
            }
          }}
        >
          {this.state.loading && <i className="fa fa-spin fa-spinner" />} Add
          New
        </button>
      </WrapLayout>
    )
  }
}

export default MUINewProject
